.opened {
    display: block!important;
}

.entry-content {
    background: #F7F7F7;
}

header.header, footer.footer {
    display: none;
}

.entry-content > .woocommerce {
    max-width: 695px;
    margin: auto;
    padding-top: 40px;
    padding-bottom: 40px;
}

.popup-wrapper{
	display: none;
	position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
	background-color: rgba(255,255,255,0.95);
	z-index: 1;
}

.popup-body{
	max-width: 440px;
    margin: 20vh auto 0;
}

.close-popup img{
	position: absolute;
    max-width: 40px;
    top: 15px;
    right: 15px;
}

#page-changePassword-template input{
	width: 100%;
	font-size: 16px;
	min-height: 48px;
}

.changePaswd{
	font-family: 'Roboto', sans-serif;
	font-weight: 600;
    font-size: 24px;
    line-height: 22px;
	display: block!important;
	width: 100%;
	margin-bottom: 20px;
    text-transform: uppercase;
}

#page-changePassword-template .btn{
	text-transform: uppercase;
    font-size: 18px;
    border: none;
    padding: 10px;
	color: #FFF;
}

#emailTxt, #emailBtn {
	width: 100%;
}

.wrapper.login .woocommerce-form-login, body.woocommerce-order-received .heading_section{
    display: none;
}

body#dark .woocommerce-billing-fields__field-wrapper .wrapper, 
body#dark .woocommerce-checkout>.wrapper, 
body#dark .wrapper.login, 
body#dark .wrapper.login .woocommerce-info,
body#dark .woocommerce-error,
body#dark .wrapper.order-details, 
body#dark .wrapper.order-receipt,
body#dark .wrapper.order-failed,
body#dark .woocommerce-message
{
	color: #FFF;
	background: #202326!important;
	box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.05);
}

body#dark.woocommerce-checkout .entry-content {
	background-color: #17191C;	
}

body#dark .woocommerce-billing-fields__field-wrapper {
    background: #17191C;
}

body#dark .checkout_coupon #coupon_code,
body#dark .woocommerce-input-wrapper input, 
body#dark .woocommerce-input-wrapper select,

body#dark .woocommerce-input-wrapper textarea,
body#dark .wrapper.login .woocommerce-form-login input
{
	background-color: rgba(255, 255, 255, .05)!important;
	color: #FFFFFF!important;
}

body#dark .woocommerce-input-wrapper select option {
    background-color: #17191C;
}

body#dark .checkout_coupon #coupon_code::placeholder,
body#dark .checkout_coupon #coupon_code:-ms-input-placeholder,
body#dark .checkout_coupon #coupon_code::-ms-input-placeholder,
body#dark .woocommerce-input-wrapper input::placeholder, 
body#dark .woocommerce-input-wrapper input:-ms-input-placeholder, 
body#dark .woocommerce-input-wrapper input::-ms-input-placeholder,
body#dark .woocommerce-input-wrapper select::placeholder,
body#dark .woocommerce-input-wrapper select:-ms-input-placeholder,
body#dark .woocommerce-input-wrapper select::-ms-input-placeholder,
body#dark .woocommerce-input-wrapper textarea::placeholder,
body#dark .woocommerce-input-wrapper textarea:-ms-input-placeholder,
body#dark .woocommerce-input-wrapper textarea::-ms-input-placeholder,
body#dark .wrapper.login .woocommerce-form-login input::placeholder, 
body#dark .wrapper.login .woocommerce-form-login input:-ms-input-placeholder,
body#dark .wrapper.login .woocommerce-form-login input::-ms-input-placeholder
{
    color: rgba(255, 255, 255, .3)!important;
}

body#dark .wrapper.order-procced .woocommerce-checkout-payment {
	background: #202326!important;
}

body#dark .woocommerce-billing-fields__field-wrapper .wrapper h2,
body#dark .woocommerce-error li, body#dark .woocommerce-message,
body#dark .woocommerce form .form-row label, 
body#dark .woocommerce-form-register p,
body#dark .woocommerce form .form-row.woocommerce-invalid label,
body#dark .wrapper.order-details h3,
body#dark .wrapper.order-details .cart_item .product-name,
body#dark .checkout_coupon > p,
body#dark .wrapper.login .woocommerce-form-login .lost_password a,
body#dark .wrapper.order-details p,
body#dark .wrapper.order-receipt h2,
body#dark .wrapper.order-details ul.woocommerce-thankyou-order-details li,
body#dark .wrapper.order-receipt table.shop_table td.product-name,
body#dark .wrapper.order-receipt tfoot td,
body#dark .wrapper.order-receipt tbody .product-total,
body#dark .wrapper.order-receipt .order_details td,
body#dark .wrapper.order-receipt .order_details th
{
	color: #fff!important;
}

body#dark .wrapper.order-details ul.woocommerce-thankyou-order-details li {
    border-bottom: 1px solid rgba(255,255,255,0.3);
}

body#dark .woocommerce table.shop_table tbody th,
body#dark .woocommerce table.shop_table tfoot td,
body#dark .woocommerce table.shop_table tfoot th {
    border-top: 1px solid rgba(255,255,255,0.3);
}

body#dark .blue-cards label {
	background-color: rgba(255, 255, 255, .05)!important;
	border: 1px solid rgba(51,51,51,0.05);
}

body#dark .blue-cards label.active {
	background-color: rgba(255, 255, 255, .3)!important;
    border: 1px solid rgba(51,51,51,.3);
}

body#dark .back-to-shop a {
	background-color: rgb(255, 255, 255)!important;
    border: 1px solid rgba(51, 51, 51, .5)!important;
}

body#dark .woocommerce-account-fields {
	border: 1px solid rgb(51,51,51);
}

body#dark form.checkout label {
	color: #fff;
}

body#dark .terms-popup {
	background: rgba(255, 255, 255, 0.3);
}

body#dark .terms-popup i{
	color: #fff;
}

body#dark .woocommerce-checkout.processing > .blockUI.blockOverlay {
    background: rgba(255, 255, 255, 0.3)!important;
}

body#dark.woocommerce-checkout #main-content {
    background: #17191C;
}

body#dark .wrapper.order-receipt .woocommerce-customer-details .woocommerce-column p {
    color: #fff!important;
}

body#dark .woocommerce .woocommerce-customer-details address {
    border: 1px solid rgba(255,255,255,0.3);
}



@media (max-width: 767px) {

    .entry-content > .woocommerce {
        padding: 20px 0;
        margin: 0 20px;
    }
    
    .popup-body{
        max-width: 440px;
        margin: 20vh 30px 0;
    }

}